import React, { Component } from "react";
import { Form, Button } from "react-bootstrap";
import { connect } from "react-redux";
import IntlTelInput from 'react-intl-tel-input';
import 'react-intl-tel-input/dist/main.css';
import { getIPDetails, otherInfoSkip, otherInfoUpate } from '../../actions/loginActions'
import FormTerms from "./Components/FormTerms";

const isValidNumber = (n) => {
    return /^[1-9]\d{9,14}$/.test(n);
}

class MoreInfo extends Component {
    constructor(props) {
        super(props)
        //this.getIpDetails();
    }


    state = { accountCompanyName: null, phoneError: null, phone: null, country_code: null, countrySortname: null }

    onACNChange = event => this.setState({ accountCompanyName: event.target.value })

    componentDidMount(){
        this.setState({ countrySortname: localStorage.getItem('countryCode') ? localStorage.getItem('countryCode') : 'US' });
    }

    onClickToSkip = event => {
        const { getBack } = this.props
        this.props.otherInfoSkip(getBack)
    }

    onClickToFinish = event => {
        const { getBack } = this.props
        const { accountCompanyName, phone, country_code, phoneError } = this.state;
        const payload = { organization: String(accountCompanyName || ''), contact_no: String(phone || ''), country_code: String(country_code || '') }
        if (accountCompanyName && String(accountCompanyName).length > 0 || (phone && country_code && !phoneError)) {
            if (phone && country_code && !phoneError && accountCompanyName && String(accountCompanyName).length > 0) {
                this.props.otherInfoUpate(payload, getBack)
            }
            else if (!phoneError && accountCompanyName && String(accountCompanyName).length > 0) {
                this.props.otherInfoUpate(payload, getBack)
            }
            else if (phone && country_code && !phoneError) {
                this.props.otherInfoUpate(payload, getBack)
            }
        }
        else this.onClickToSkip()
    }



    // getIpDetails = async () => {  
    //     try {
    //         let response = await getIPDetails();
    //         if (response && Object.keys(response).length > 0) {
    //             this.setState({ countrySortname: response.country });
    //         } else {
    //             this.setState({ countrySortname: 'US' });
    //         }

    //     } catch (e) {
    //         this.setState({ countrySortname: 'US' });
    //     }
    // }

    updatePhoneNumber1 = (isValid, value, selectedCountryData) => {
        if (value) {
            if (isValidNumber(value)) {
                this.setState({ phone: value, country_code: selectedCountryData.dialCode, phoneError: false })
            }
            else {
                this.setState({ phoneError: true, phone: value })
            }
        }
        else this.setState({ phoneError: false, phone: value })
    }

    render() {
        const { accountCompanyName, countrySortname, phoneError } = this.state;

        return <div className="w-100 mb-lg-0 mb-3">
                    <div className="text-start">
                        <h1 className=" mb-0 display-2 fw-bold mb-5">You're nearly there</h1>
                    </div>
                    <div className="mb-8">
                        <Form.Group className="mb-4 mb-xl-5">
                            <Form.Label className="fw-bold " htmlFor="comp_name">Company Name</Form.Label>
                            <Form.Control size="lg" placeholder="Company Name" id="comp_name" className={`mb-1`} value={accountCompanyName} onChange={this.onACNChange} />
                        </Form.Group>
                        <Form.Group className="mb-4 mb-xl-5">
                            <Form.Label className="fw-bold " htmlFor="comp_name">Contact No</Form.Label>
                            <IntlTelInput key={countrySortname} fieldId="phone" preferredCountries={[countrySortname ? countrySortname : 'US']} onPhoneNumberChange={this.updatePhoneNumber1}
                                onPhoneNumberBlur={this.updatePhoneNumber1}
                                containerClassName={`intl-tel-input w-100 ${phoneError ? 'is-invalid mb-1' : ''}`} inputClassName={`form-control form-control-lg w-100 ${phoneError ? 'is-invalid' : ''}`} autocomplete="off" data-intl-tel-input-id="0" placeholder="9987654321" />
                            {phoneError ? <span className="invalid-feedback">Please enter a valid Phone Number</span> : null}
                        </Form.Group> 
                    </div>
                    <Form.Group className="mb-4 mb-xl-5">
                        <Button size="lg" onClick={()=> countrySortname ? this.onClickToFinish() : ''} id="finish_info" className="w-100">Finish</Button>
                    </Form.Group>
                    <Form.Group className="mb-4 mb-xl-5 text-center">
                        <Button variant="link text-gray-400 fw-medium active-primary" onClick={() => countrySortname ? this.onClickToSkip() : ''} id="skip_info">Skip</Button>
                    </Form.Group>
                    <FormTerms/>
                </div>
    }
}
const mapStateToProps = (state) => {  
    return {
        signUpData: state.signUp?.signUpData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        otherInfoSkip: (getBack) => dispatch(otherInfoSkip(getBack)),
        otherInfoUpate: (data, getBack, redirect) => dispatch(otherInfoUpate(data, getBack, redirect)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(MoreInfo);